<template>
  <div>
    <p class="grey--text text-subtitle-1">{{ subMateri.title }}</p>
    <p class="grey--text mt-n4 text-subtitle-2">
      {{ dateFilter(subMateri.createdAt) }}
    </p>
    <div v-html="subMateri.content"></div>
    <div class="d-flex mt-10">
      <v-btn
        color="primary"
        text
        dark
        v-if="showPrevious"
        @click="bus.$emit('previous-materi', idMateri)"
      >
        <v-icon left>mdi-arrow-left-circle-outline</v-icon>
        Previous
      </v-btn>
      <v-spacer></v-spacer>
      <v-btn
        color="primary"
        text
        dark
        @click="bus.$emit('next-materi', idMateri)"
        v-if="showNext"
      >
        Next
        <v-icon right>mdi-arrow-right-circle-outline</v-icon>
      </v-btn>
    </div>
  </div>
</template>

<script>
import { onMounted, ref, computed, watch } from "@vue/composition-api";
export default {
  name: "Materi",
  props: ["mapel", "user"],
  setup(props, { root }) {
    const subMateri = ref({
      title: "",
      content: "",
    });
    const materi = ref({});
    const showNext = ref(false);
    const showPrevious = ref(false);
    const idMateri = computed(() => root.$route.params.idMateri);
    const newMateri = () => {
      const bab = props.mapel.bab;
      for (const i in bab) {
        for (const j in bab[i].materi) {
          const sub = bab[i].materi[j].subMateri.find(
            (el) => el._id === idMateri.value
          );
          const currentMateri = bab[i].materi[j].subMateri.some(
            (el) => el._id === idMateri.value
          );
          if (currentMateri) {
            materi.value = bab[i].materi[j];
          }
          if (sub) {
            subMateri.value = sub;
            break;
          }
        }
      }

      root.axios
        .post("user/completed/", {
          idUser: props.user._id,
          idSubMateri: idMateri.value,
        })
        .then(({ data }) => {
          if (data.complete !== undefined) {
            root.bus.$emit("resetCompleted", data.complete.complete);
          }
        });
      const index = materi.value.subMateri.findIndex(
        (x) => x._id === idMateri.value
      );
      index >= materi.value.subMateri.length - 1
        ? (showNext.value = false)
        : (showNext.value = true);
      index === 0 ? (showPrevious.value = false) : (showPrevious.value = true);
    };
    const dateFilter = (date) => {
      const monthNames = [
        "Januari",
        "Februari",
        "Maret",
        "April",
        "Mei",
        "Juni",
        "Juli",
        "Agustus",
        "September",
        "Oktober",
        "November",
        "December",
      ];
      date = new Date(date);
      return `${date.getDate()} ${
        monthNames[date.getMonth()]
      } ${date.getFullYear()}`;
    };
    onMounted(() => {
      newMateri();
    });
    watch(idMateri, newMateri);
    return { materi, idMateri, dateFilter, subMateri, showNext, showPrevious };
  },
};
</script>

